import React, { Component } from "react";
import Slider from "react-slick";

class SimpleSlider extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      arrows: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };

    const { images } = this.props;
    console.log(images, "in slider");
    return (
      <div>
        <Slider {...settings}>
          <div>
            {images.map((item, i) => {
              return (
                <div
                  key={i}
                  style={{
                    width: "100%",
                    height: "400px",
                    background: `url(${item.url}) center/cover no-repeat`
                  }}
                ></div>
              );
            })}
          </div>
        </Slider>
      </div>
    );
  }
}

export default SimpleSlider;
